.clearfix::after {
	content: '';
	display: block;
	clear: both;
}

html {
	font-size: 10px;
}

body, ul, li, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-weight: normal;
  list-style: none;
}

a {
	text-decoration: none;
}

input, textarea, 
input:focus, textarea:focus,
button {
	outline: none;
	border: none;
}

.text-shadow {
	text-shadow: 1px 1px 1px #595959;
}

.app-logo {
	display: block;
	cursor: pointer;
	padding: 0;
	width: 70px;
	height: 70px;
	margin: 30px auto;
	-webkit-box-shadow: 3px 3px 5px 0 #595959;
	        box-shadow: 3px 3px 5px 0 #595959;
	border-radius: 50%;
	-webkit-transition: .2s;
	-o-transition: .2s;
	transition: .2s;
}

.app-logo:hover {
	-webkit-transform: rotate(-3deg) scale(1.02);
	    -ms-transform: rotate(-3deg) scale(1.02);
	        transform: rotate(-3deg) scale(1.02);
	-webkit-box-shadow: 4px 4px 10px 2px #595959;
	        box-shadow: 4px 4px 10px 2px #595959;
	-webkit-animation: logo-shadow .7s ease .5s infinite alternate;
	        animation: logo-shadow .7s ease .5s infinite alternate;
}

@-webkit-keyframes logo-shadow {
	from {-webkit-box-shadow: 4px 4px 10px 2px #595959;box-shadow: 4px 4px 10px 2px #595959;}
	to {-webkit-box-shadow: 4px 4px 10px 3px #595959;box-shadow: 4px 4px 10px 3px #595959;}
}

@keyframes logo-shadow {
	from {-webkit-box-shadow: 4px 4px 10px 2px #595959;box-shadow: 4px 4px 10px 2px #595959;}
	to {-webkit-box-shadow: 4px 4px 10px 3px #595959;box-shadow: 4px 4px 10px 3px #595959;}
}

.app-logo__img {
	background: url('../img/logo.svg') no-repeat;
	width: 101%;
	height: 101%;
}

.board__container,
.list__container {
	width: 85%;
	margin: 30px auto;
}

.board__elem {
	display: inline-block;
	width: 320px;
	margin: 0 15px 30px 15px;
	border-radius: 5px;
	-webkit-box-shadow: 0 4px 10px 2px #595959;
	        box-shadow: 0 4px 10px 2px #595959;
	-webkit-transition: -webkit-box-shadow .2s, -webkit-transform .2s;
	transition: -webkit-box-shadow .2s, -webkit-transform .2s;
	-o-transition: box-shadow .2s, transform .2s;
	transition: box-shadow .2s, transform .2s;
	transition: box-shadow .2s, transform .2s, -webkit-box-shadow .2s, -webkit-transform .2s;
	text-align: center;
	padding: 35px 0;
	font-size: 2.2rem;
	vertical-align: top;
	cursor: pointer;
}

.board__elem:hover,
.board-creator:hover,
.list-creator__link:hover {
	-webkit-transform: scale(1.03);
	    -ms-transform: scale(1.03);
	        transform: scale(1.03);
	-webkit-box-shadow: 0 6px 15px 3px #595959;
	        box-shadow: 0 6px 15px 3px #595959;
}

.board-elem__link {
	background-color: #9fe7a4;
	color: white;
}

.board-creator {
	background-color: #9fe7a4;
	border-radius: 5px;
	-webkit-box-shadow: 0 4px 10px 0 #595959;
	        box-shadow: 0 4px 10px 0 #595959;
	display: inline-block;
	width: 320px;
	margin: 0 15px 30px 15px;
	-webkit-transition: -webkit-box-shadow .2s, -webkit-transform .2s;
	transition: -webkit-box-shadow .2s, -webkit-transform .2s;
	-o-transition: box-shadow .2s, transform .2s;
	transition: box-shadow .2s, transform .2s;
	transition: box-shadow .2s, transform .2s, -webkit-box-shadow .2s, -webkit-transform .2s;
	padding: 40px;
	cursor: pointer;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	vertical-align: top;
}

.board-creator__header {
	height: 4rem;
	border-bottom: 1px solid white;
}

.board-creator__title {
	color: white;
	font-size: 2rem;
	float: left;
}

.board-creator__close {
	background: url('../img/closeIcon.svg') 100% 100% no-repeat;
	float: right;
	height: 25px;
	width: 25px;
	-webkit-transition: .2s;
	-o-transition: .2s;
	transition: .2s;
	cursor: pointer;
}

.board-creator__close:hover {
	-webkit-transform: rotate(90deg) scale(1.2);
	    -ms-transform: rotate(90deg) scale(1.2);
	        transform: rotate(90deg) scale(1.2);
}

.board-creator__content {
	padding-top: 30px;
}

.board-creator__text {
	font-size: 1.3rem;
	color: white;
}

.board-creator__input {
	display: block;
	margin: 20px 0 20px 0;
	padding: 12px 15px;
	border-radius: 5px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 100%;
	font-size: 1.8rem;
	-webkit-box-shadow: 0 3px 5px 0 #595959;
	        box-shadow: 0 3px 5px 0 #595959;
	color: #595959;
}

.board-creator__button {
	border-radius: 5px;
	background-color: #ddd;
	border: none;
	padding: 15px 20px;
	-webkit-box-shadow: 0 3px 5px 0 #595959;
	        box-shadow: 0 3px 5px 0 #595959;
	-webkit-transition: .1s;
	-o-transition: .1s;
	transition: .1s;
	cursor: pointer;
}

.board-creator__button:active {
	-webkit-transform: translateY(2px);
	    -ms-transform: translateY(2px);
	        transform: translateY(2px);
	-webkit-box-shadow: 0 2px 3px 0 #595959;
	        box-shadow: 0 2px 3px 0 #595959;
	outline: none;
}

.board-creator__button:last-child {
	margin-left: 15px;
}

.board__title {
	display: inline-block;
	font-size: 2.5rem;
	font-weight: bold;
	color: white;
	border-radius: 5px;
	padding: 20px 40px;
	background-color: #9fe7a4;
	-webkit-box-shadow: 0 3px 5px 1px #595959;
	        box-shadow: 0 3px 5px 1px #595959;
	margin-bottom: 50px;
}

.list-creator__link {
	display: inline-block;
	width: 160px;
	height: 120px;
	color: white;
	font-size: 1.7rem;
	background-color: #0a2c74;
	text-align: center;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	padding-top: 50px;
	border-radius: 5px;
	-webkit-box-shadow: 0 4px 10px 0 #595959;
	        box-shadow: 0 4px 10px 0 #595959;
	-webkit-transition: -webkit-box-shadow .2s, -webkit-transform .2s;
	transition: -webkit-box-shadow .2s, -webkit-transform .2s;
	-o-transition: box-shadow .2s, transform .2s;
	transition: box-shadow .2s, transform .2s;
	transition: box-shadow .2s, transform .2s, -webkit-box-shadow .2s, -webkit-transform .2s;
	cursor: pointer;
}

.list-creator {
	-webkit-box-shadow: 0 5px 15px 0 #595959;
	        box-shadow: 0 5px 15px 0 #595959;
	display: inline-block;
	padding: 15px 20px;
	position: relative;
	width: 250px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.list-creator__input,
.list-elem__input {
	display: block;
	margin-bottom: 20px;
	padding: 12px 15px;
	border-radius: 5px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 100%;
	font-size: 1.8rem;
	-webkit-box-shadow: 0 2px 10px 0 #595959;
	        box-shadow: 0 2px 10px 0 #595959;
	color: #595959;
}

.list-creator__close {
	background: url('../img/closeIcon.svg') 100% 100% no-repeat;
	height: 25px;
	width: 25px;
	position:absolute;
	top: -10px;
	right: -10px;
	-webkit-transition: .2s;
	-o-transition: .2s;
	transition: .2s;
	cursor: pointer;
}

.list-creator__close:hover {
	-webkit-transform: rotate(90deg) scale(1.2);
	    -ms-transform: rotate(90deg) scale(1.2);
	        transform: rotate(90deg) scale(1.2);
}

.list-elem {
	display: inline-block;
	width: 250px;
	margin: 0 20px 20px 20px;
	-webkit-box-shadow: 0 4px 10px 0 #595959;
	        box-shadow: 0 4px 10px 0 #595959;
	vertical-align: top;
	text-align: center;
	padding: 0 8px;
}

.list-elem__title {
	color: #595959;
	font-size: 1.8rem;
	margin-top: 15px;
}

.list-elem__line {
	background-color: #595959;
	height: 1px;
	margin: 15px 0;
}

.todo-elem {
	background-color: #caffde;
	margin: 8px 0;
	padding: 12px 6px;
	border-radius: 5px;
	-webkit-box-shadow: 0 2px 6px 0 #595959;
	        box-shadow: 0 2px 6px 0 #595959;
	cursor: -webkit-grab;
	cursor: grab;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.todo-elem.checked {
	background-color: #f3ecff;
}

.todo-elem__title {
	font-size: 2rem;
	color: black;
	margin-right: 30px;
}

.todo-elem__checkbox {
	font-size: 2.2rem;
	color: #595959;
	cursor: pointer;
}

.todo-elem.checked .todo-elem__title,
.todo-elem.checked .todo-elem__checkbox {
	color: #a6a6a6;
	text-decoration: line-through;
}


